@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Baloo Tammudu 2", sans-serif;
}

.main_div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
}

.videos {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-gap: 2rem;
}

.videos .recent_uploads {
  text-transform: capitalize;
  border-bottom: 1px solid rgb(190, 190, 190);
}
.video {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.video .thumb_img {
  width: 75%;
  height: 100%;
  position: relative;
}

.thumb_img .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  opacity: 0;
  transition: 0.4s ease;
}

.thumb_img .content a {
  padding: 0.5rem;
  display: block;
  font-size: 2.5rem;
  /* text-decoration: none; */
  color: #000;
  -webkit-transform: translateY(2rem);
          transform: translateY(2rem);
  transition: -webkit-transform 0.25s ease;
  transition: transform 0.25s ease;
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
}

.video .thumb_img:hover .content {
  opacity: 1;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

.video .thumb_img:hover .content a {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.video img {
  width: 100%;
  height: 100%;
}
.details {
  padding: 0 1rem;
}
.desc {
  border-bottom: 1px solid rgb(190, 190, 190);
  padding-bottom: 0.5rem;
}

.video h2 {
  margin-top: 1rem;
}

.video p:nth-child(2),
.video p:nth-child(3),
.video p:nth-child(4),
.video p:nth-child(5) {
  color: #948c81;
}

footer {
  background: #f1f1f1;
}
footer p {
  padding: 1rem;
  margin: 0;
}

.header {
  height: 75vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.header h1,
.header p {
  -webkit-animation: header 1s ease forwards;
          animation: header 1s ease forwards;
}

.header h1 {
  position: relative;
}

.header h1::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #111;
  -webkit-animation: line 1s ease 1.5s forwards;
          animation: line 1s ease 1.5s forwards;
}

@-webkit-keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@-webkit-keyframes header {
  0% {
    -webkit-transform: translateY(5rem) scale(0.6);
            transform: translateY(5rem) scale(0.6);
  }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

@keyframes header {
  0% {
    -webkit-transform: translateY(5rem) scale(0.6);
            transform: translateY(5rem) scale(0.6);
  }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

.about_head {
  text-align: center;
  border-bottom: 1px solid rgb(190, 190, 190);
}

@media (max-width: 768px) {
  .video {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .video .thumb_img {
    width: 100%;
  }
  .header {
    height: 87vh;
  }
}

